import {
    quoteKey,
} from '../utils/key';
import { isNonEmptyArray } from '../utils/array';
import {
    isNonEmptyString,
    isString,
} from '../utils/string';
import { parseKeypath } from './keypath-parser';

export function isListEntry(path) {
    // eslint-disable-next-line no-use-before-define
    return pathToObjects(path).pop().listEntry === true;
}

export function pathWithoutKeys(path) {
    if (!path) return path;
    // eslint-disable-next-line no-use-before-define
    return pathToObjects(path, { ignoreListEntries: true }).pop().path;
}

export const isListKeyPath = path =>
    // eslint-disable-next-line no-use-before-define
    !!pathToObjects(path).some(o => isNonEmptyArray(o.keys));

export function pathWithoutKeyInstances(path) {
    // eslint-disable-next-line no-use-before-define
    return pathToObjects(path, { ignoreListKeys: true }).pop().path;
}

const listSchemaPathRE = /.*\{}/;

export const isListSchemaPath = path => listSchemaPathRE.test(path);

const splitKeyNS = (part) => {
    const [ns, key] = part.split(':');
    return key === undefined ? [undefined, ns] : [ns, key];
};

const namespaceSchemaPath = (schemaPath, rootSchemaPath) => {
    const schemaParts = schemaPath.split('/');
    const rootSchemaParts = rootSchemaPath.split('/');
    const newPath = [];
    const len = schemaParts.length < rootSchemaParts.length
        ? schemaParts.length
        : rootSchemaParts.length;

    for (let i = 0; i < len; i += 1) {
        // eslint-disable-next-line no-unused-vars
        const [schemaNS] = splitKeyNS(schemaParts[i]);
        // eslint-disable-next-line no-unused-vars
        const [, keypathKey] = splitKeyNS(rootSchemaParts[i]);
        newPath.push(schemaNS ? `${schemaNS}:${keypathKey}` : keypathKey);
    }
    return newPath.join('/');
};

export const schemaPathToKeypath = (schemaPath, rootKeypath) => {
    if (isListKeyPath(schemaPath)) return schemaPath;

    let rootSchemaPath = pathWithoutKeys(rootKeypath);
    const orgRootSchemaPath = rootSchemaPath;

    if (rootKeypath.endsWith('}')) {
        rootSchemaPath = `${rootSchemaPath}{}`;
    }
    if (schemaPath === rootSchemaPath) {
        return rootKeypath;
    }

    if (isListSchemaPath(schemaPath)) {
        return pathWithoutKeys(schemaPath).replace(
            namespaceSchemaPath(schemaPath, orgRootSchemaPath),
            rootKeypath,
        );
    }

    return schemaPath.replace(namespaceSchemaPath(schemaPath, orgRootSchemaPath), rootKeypath);
};

function getLastToken(tokens) {
    if (!tokens) return undefined;
    if (tokens.length === 0) return undefined;
    return tokens[tokens.length - 1];
}

function validLastToken(token) {
    switch (token.type) {
    case ':':
        return false;
    case '{':
        return false;
    case 'key':
        return false;
    default:
        return true;
    }
}

function parse(path, options) {
    let result;
    try {
        const tokens = parseKeypath(path, options);
        const lastToken = getLastToken(tokens);
        if (!validLastToken(lastToken)) {
            const lastTokenError = new Error();
            lastTokenError.name = 'SyntaxError';
            lastTokenError.offset = lastToken.offset;
            lastTokenError.tokens = tokens;
            throw lastTokenError;
        }

        result = {
            status: 'ok',
            tokens,
        };
    } catch (e) {
        result = {
            status: e.name,
            message: e.message,
            errorOffset: e.offset,
            tokens: e.tokens || [e.name],
        };
    }
    return result;
}

function getNS(parent, current) {
    if (current.ns) {
        return current.ns;
    }

    if (parent && parent.ns) {
        return parent.ns;
    }

    return current.ns;
}

function getRelativeName(parent, current) {
    if (!parent && !current.ns) {
        return current.name;
    }

    if (!parent && current.ns) {
        return `${current.ns}:${current.name}`;
    }

    if (parent.ns !== current.ns) {
        return `${current.ns}:${current.name}`;
    }

    return current.name;
}

function getPath(parent, current) {
    if (!parent && !current.ns) {
        return `/${current.name}`;
    }

    if (!parent && current.ns) {
        return `/${current.ns}:${current.name}`;
    }

    if (parent.ns !== current.ns) {
        return `${parent.path}/${current.ns}:${current.name}`;
    }
    return `${parent.path}/${current.name}`;
}

function getUri(parent, current) {
    if (!parent && !current.ns) {
        return `/${current.name}`;
    }

    if (!parent && current.ns) {
        return `/${current.ns}:${current.name}`;
    }

    if (parent.ns !== current.ns) {
        return `${parent.uri}/${current.ns}:${current.name}`;
    }
    return `${parent.uri}/${current.name}`;
}

function getCurrent(acc) {
    return acc.slice(-1)[0];
}

function getParent(acc) {
    if (acc.length < 2) {
        return undefined;
    }
    return acc.slice(-2)[0];
}

const populateUniqueText = (j, res, allTokens) => {
    const results = res;
    for (let i = 0; i < allTokens.length; i += 1) {
        if (!results[i].unique && j < allTokens[i].length) {
            results[i].index = j;
            results[i].text = allTokens[i][j].text + results[i].text;
        }
    }
};

const checkUniqueness = (res) => {
    const results = res;
    let isAllUnique = true;
    for (let i = 0; i < results.length; i += 1) {
        results[i].unique = true;
        for (let k = 0; k < results.length; k += 1) {
            if (i !== k && results[i].text === results[k].text) {
                isAllUnique = false;
                results[i].unique = isAllUnique;
                break;
            }
        }
    }

    return isAllUnique;
};

export const getTruncatedPaths = (paths, maxTokens = 20) => {
    const allTokens = paths.map(parse)
        .map(result => (result.status !== 'ok' ? [{ text: '' }] : result.tokens))
        .map(t => t.reverse());

    const results = paths.map(() => ({ text: '' }));
    let isAllUnique = false;
    for (let j = 0; !isAllUnique && j < maxTokens; j += 1) {
        populateUniqueText(j, results, allTokens);

        isAllUnique = checkUniqueness(results);
    }

    const trunks = results.map((r, i) => {
        let pad = '';
        for (let j = r.index + 1; j < allTokens[i].length && allTokens[i][j].text !== '/'; j += 1) {
            pad = allTokens[i][j].text + pad;
        }
        return pad + r.text;
    });

    return paths.reduce((acc, p, i) => ({ ...acc, [p]: trunks[i] }), {});
};

export function pathToObjects(path, options) {
    const parsed = parse(path, { options });
    if (parsed.status === 'SyntaxError') throw new Error(parsed.message);
    return parsed.tokens.reduce((acc, cur) => {
        const current = getCurrent(acc);
        const parent = getParent(acc);
        switch (cur.type) {
        case '/':
            acc.push({ path: '' });
            break;
        case 'name':
            current.name = cur.text;
            current.ns = getNS(parent, current);
            current.parentPath = parent ? parent.path : '';
            current.parentUri = parent ? parent.uri : '';
            current.path = getPath(parent, current);
            current.uri = getUri(parent, current);
            current.type = 'name';
            current.relativeName = getRelativeName(parent, current);
            acc.splice(-1, 1, current);
            break;
        case '{':
            acc.push({ path: '', keys: [] });
            break;
        case 'key':
            current.keys.push(cur.key);
            current.type = 'key';
            break;
        case '}':
            current.ns = getNS(parent, current);
            current.parentPath = parent.path;
            current.parentUri = parent.uri;
            current.name = current.keys.map(k => quoteKey(k)).join(' ');
            current.relativeName = `${parent.relativeName}{${current.name}}`;
            current.path = `${parent.path}{${current.name}}`;
            current.uri = `${parent.uri}{${encodeURIComponent(current.name)}}`;
            current.listEntry = true;
            parent.list = true;
            acc.splice(-1, 1, current);
            break;
        case 'ns':
            current.ns = cur.text;
            break;
        default:
            break;
        }
        return acc;
    }, []);
}

export function namespaceIsNeeded(parentPath, qname) {
    const parent = pathToObjects(parentPath).pop();
    const ns = (qname || '').split(':')[0];
    return (ns && parent.ns !== ns);
}

export function getChildName(parentPath, name, qname) {
    return namespaceIsNeeded(parentPath, qname) ?
        qname : name;
}

export function pathConcat(parentPath, name, qname) {
    const childName = getChildName(parentPath, name, qname);
    return `${parentPath}/${childName}`;
}

export const getListPath = (keypath, basepath) => {
    if (!isNonEmptyString(keypath) || !keypath.startsWith('/')) {
        return null;
    }
    if (!isNonEmptyString(basepath) || !basepath.startsWith('/')) {
        return null;
    }
    const o1 = pathToObjects(keypath);
    const o2 = pathToObjects(basepath);
    const shortest = o1.length > o2.length ? o2.length : o1.length;

    let i = 0;
    while (i < shortest) {
        if (o1[i].name !== o2[i].name) {
            break;
        }
        i += 1;
    }
    if (i === o1.length) return null;

    const listNode = o1.slice(i - 1).find(kp => kp.list === true);
    return listNode ? listNode.path : null;
};

export const findCommonAncestor = (keypath, schemapath) => {
    const keypathObjects = pathToObjects(keypath);
    const o1 = keypathObjects.filter(o => o.type !== 'key');
    const o2 = pathToObjects(schemapath);

    let shortest = o1.length;

    if (o1.length > o2.length) {
        shortest = o2.length;
    }

    let i = 0;
    const objects = [];

    // Iterate both paths without keys
    while (i < shortest) {
        if (o1[i].name === o2[i].name) {
            objects.push(o1[i]);
            i += 1;
        } else {
            break;
        }
    }
    if (objects.length < 1) return undefined;

    // Check if found common path has a key as a child and use it instead
    let path = objects[objects.length - 1].path;
    const child = keypathObjects.find(o => o.parentPath === path);
    if (child && child.type === 'key') {
        path = child.path;
    }
    return path;
};

export const getNodePathWithKeys = (keypath, nodepath) => {
    const keyobjects = pathToObjects(keypath);
    const nodepobjects = pathToObjects(nodepath);
    let lastIndex = -1;
    return nodepobjects.reduce((acc, nodeObject) => {
        acc.push(nodeObject);
        const foundIndex = keyobjects.findIndex((ko, j) =>
            (lastIndex < j && ko.name === nodeObject.name && ko.ns === nodeObject.ns));

        if (foundIndex === -1) return acc;

        lastIndex = foundIndex;

        if (keyobjects[foundIndex].list) {
            lastIndex += 1;
            acc.push(keyobjects[lastIndex]);
        }
        return acc;
    }, []).reduce((acc, o) => {
        if (o.type === 'key') {
            return `${acc}{${o.keys.join(' ')}}`;
        }
        const prefix = o.ns ? `${o.ns}:` : '';
        return `${acc}/${prefix}${o.name}`;
    }, '');
};

export const stripItemsFromPath = (path, noOfItems) => {
    const objects = pathToObjects(path).slice(0, -noOfItems);
    if (objects.length === 0) return '/';
    return objects[objects.length - 1].path;
};

export const getPrefixFromPath = (path) => {
    if (!path) return undefined;
    const objs = pathToObjects(path);
    return objs.slice(-1)[0].ns;
};

export const getNameRelativeToParent = parent => (child) => {
    const prefix = isString(parent) ? getPrefixFromPath(parent) : parent.ns;
    const { name, qname } = (child.data || child);
    return qname.startsWith(`${prefix}:`) ? name : qname;
};

export const getLastListEntryKeypath = (keypath) => {
    const keyEnd = keypath.lastIndexOf('}');
    if (keyEnd > 0) return keypath.substring(0, keyEnd + 1);

    return keypath;
};

export {
    getLastToken as _getLastToken,
    parse as _parse,
    validLastToken as _validLastToken,
};
