export const fileExists = async ({
    opfsDir,
    filename,
}) => {
    try {
        const fileHandle = await opfsDir.getFileHandle(filename);
        return fileHandle?.kind === 'file';
    } catch (e) {
        if (e?.name === 'NotFoundError') {
            return false;
        }
        throw e;
    }
};

export const downloadFile = async ({
    opfsRoot,
    destinationFile,
    location,
    funcs: {
        fetchFn = fetch,
    } = {},
}) => {
    const response = await fetchFn(location);
    const arrayBuffer = await response.arrayBuffer();
    const fileHandle = await opfsRoot.getFileHandle(destinationFile, { create: true });
    const syncAccessHandle = await fileHandle.createSyncAccessHandle();
    const dataview = new DataView(arrayBuffer);
    syncAccessHandle.write(dataview);
    syncAccessHandle.flush();
    syncAccessHandle.close();
};

export const getFile = async ({
    location,
    filename,
    forceReload,
    funcs: {
        navigator_ = navigator,
        fileExists_ = fileExists,
        downloadFile_ = downloadFile,
    } = {},
}) => {
    const opfsRoot = await navigator_.storage.getDirectory();
    const exists = await fileExists_({ opfsDir: opfsRoot, filename });
    const shouldFetch = !exists || forceReload;
    if (shouldFetch) {
        await downloadFile_({ opfsRoot, destinationFile: filename, location });
    }
    return { filename, isCached: !shouldFetch };
};

export const removeEntry = async ({
    filename,
    funcs: {
        navigator_ = navigator,
    } = {},
} = {}) => {
    if (!navigator_.storage) {
        return [];
    }
    const opfsRoot = await navigator_.storage.getDirectory();
    const entries = await opfsRoot.entries();
    const removed = [];
    for await (const [, value] of entries) {
        if (value.name === filename) {
            await opfsRoot.removeEntry(value.name);
            removed.push(value.name);
        }
    }
    return removed;
};

