/* eslint-env worker */
/* eslint-disable no-restricted-globals */
import { getFile, removeEntry, removeAllEntries } from '@webui-one/core/utils/opfs';
import { getFilenameFromUrl } from '@webui-one/core/utils/path';
import { expose } from '@webui-one/core/utils/worker';
import sqlite3InitModule from '@sqlite.org/sqlite-wasm';

const getMemoryDb = async (sqlite3, location) => {
    const response = await fetch(location);
    const arrayBuffer = await response.arrayBuffer();
    const p = sqlite3.wasm.allocFromTypedArray(arrayBuffer);
    const db = new sqlite3.oo1.DB();
    const rc = sqlite3.capi.sqlite3_deserialize(
        db.pointer,
        'main',
        p,
        arrayBuffer.byteLength,
        arrayBuffer.byteLength,
        sqlite3.capi.SQLITE_DESERIALIZE_FREEONCLOSE,
        // Optionally:
        // | sqlite3.capi.SQLITE_DESERIALIZE_RESIZEABLE
    );
    db.checkRc(rc);
    return db;
};

function formatSqliteError(cause) {
    const index = cause.message.indexOf(':');

    if (cause.message.startsWith('sqlite3') && index > 0) {
        // NOTE: Strip "sqlite3 result code xx:" from the
        // error message before rethrowing it.
        return cause.message.substring(index + 1).trim();
    }

    return 'Unspecified database error';
}

const getOpfsDb = async (sqlite3, dbUrl) => {
    const filename = getFilenameFromUrl(dbUrl);
    let retries = 1;
    while (retries >= 0) {
        /* eslint-disable no-await-in-loop */
        try {
            await getFile({ location: dbUrl, filename });
            const db = new sqlite3.oo1.OpfsDb(filename);
            const resultRows = [];
            const sql = 'SELECT name FROM sqlite_master;';
            db.exec({
                sql,
                rowMode: 'object',
                resultRows,
            });
            return db;
        } catch (cause) {
            // NOTE: If we fail to load the database we remove the file
            // and try again.
            //
            // If we fail too many times, we throw an error that we can
            // show to the user.

            if (retries === 0) {
                const message = formatSqliteError(cause);
                throw new TypeError(message, { cause });
            }

            await removeEntry({ filename });
            retries -= 1;
        }
        /* eslint-enable no-await-in-loop */
    }
    return Promise.resolve();
};


const getDb = async (sqlite3, dbUrl) => {
    if ('opfs' in sqlite3) {
        return getOpfsDb(sqlite3, dbUrl);
    }
    return getMemoryDb(sqlite3, dbUrl);
};

// eslint-disable-next-line no-console
const print = (...args) => console.log(...args);
// eslint-disable-next-line no-console
const printErr = (...args) => console.error(...args);

class ComplianceWorker {
    constructor() {
        this.db = null;
        this.sqlite3 = null;
    }

    async initialize(dbUrl) {
        this.sqlite3 = await sqlite3InitModule({
            print,
            printErr,
        });
        this.db = await getDb(this.sqlite3, dbUrl);
        return ({
            sqlite3Version: this.sqlite3.version.libVersion,
            opfs: 'opfs' in this.sqlite3,
        });
    }

    async query(sql, indexedParams) {
        try {
            const resultRows = [];
            this.db.exec({
                sql,
                rowMode: 'object',
                resultRows,
                bind: indexedParams,
            });
            return { result: resultRows };
        } catch (e) {
            return { result: [], error: e.message };
        }
    }

    // eslint-disable-next-line class-methods-use-this
    async cleanup() {
        const result = await removeAllEntries();
        return {
            result,
        };
    }

    async close() {
        this.db.close();
        return {};
    }
}

expose(new ComplianceWorker());
