import { getRootPath } from './browser';
import { pathToObjects } from '../keypath/kp-parser';
import {
    isNonEmptyString,
    isString,
    lstrip,
    partition,
    strip,
} from './string';
import { isObject } from './object';
import { last } from './array';

export const getLeafName = (keypath) => {
    const result = /\/([^/]*)$/.exec(keypath);
    return result && result.length > 1 ? result[1] : '';
};
export const getListKeyValue = (keypath) => {
    const result = /{([^}]+)}$/.exec(keypath);
    const values = [];
    if (result && result.length > 1 && typeof result[1] === 'string') {
        result[1].match(/[^\s"']+|"([^"]*)"|'([^']*)'/g).forEach((key) => {
            values.push(strip(key, '"'));
        });
    }
    return values;
};

export const stripListKeys = path => path.replace(/{([^}]*?)}$/, '');

export const normalizePath = p => (typeof p === 'string' ? p.replace(/{(.*?)}/g, '') : '');

export const getParentPath = (keypath) => {
    const result = /^(.*)\//.exec(keypath);
    return result && result.length > 1 && result[1] !== '' && result[1] !== '/' ? result[1] : keypath;
};

export const createPathFromLocation = (location) => {
    if (!isObject(location)) {
        return location;
    }

    const { basename, pathname, search, hash } = location;
    let path = (basename || '') + pathname;

    if (search && search !== '?') {
        path += search;
    }
    if (hash) {
        path += hash;
    }

    return path;
};

export const isDeviceConfig = path => /^\/ncs:devices\/device\{([^}]+)\}\/config.*/.test(path);
export const isDevice = path => /^\/ncs:devices\/device\{([^}]+)\}.*/.test(path);
export const isDeviceListItem = path => /^\/ncs:devices\/device\{([^}]+)\}$/.test(path);
export const isDeviceNedId = path => /^\/ncs:devices\/device\{([^}]+)\}\/device-type\/([^/]+)\/ned-id$/.test(path);

export const tryGetDeviceName = path => (isDevice(path) ? path.match(/\{([^}]+)\}/)[1] : '');

export const isChildOfPath = (basePath, path) => (
    path.startsWith(basePath) ||
    path.startsWith(`${basePath}{`) ||
    path.startsWith(`${basePath}/`)
);

export function getApplicationKey(pathname) {
    const root = getRootPath();
    const applicationkey = pathname.replace(`${root}/`, '');
    const slashIndex = applicationkey.indexOf('/', 1);
    if (slashIndex === -1) return applicationkey;
    return applicationkey.substr(0, slashIndex);
}

export function formatPath(path) {
    const root = getRootPath();
    const cleanPath = typeof path === 'string' ? path : '';
    return [root, lstrip(cleanPath, '/')].join('/');
}

export const escapeKey = (rowKey) => {
    const paths = pathToObjects(`/${strip(rowKey, '/')}`);
    const feh = paths[paths.length - 1].uri;
    return rowKey[0] !== '/' && feh[0] === '/' ? lstrip(feh, '/') : feh;
};

export const quotePathKeys = (path) => {
    const paths = pathToObjects(`/${strip(path, '/')}`);
    const quotedPath = paths[paths.length - 1].path;
    return path[0] !== '/' && quotedPath[0] === '/'
        ? lstrip(quotedPath, '/')
        : quotedPath;
};

export function expandQueryString(search) {
    const str = isNonEmptyString(search) && search.startsWith('?') ? search.slice(1) : '';
    return str.split('&').reduce((acc, param) => {
        const parts = partition('=', param);
        const name = decodeURIComponent(parts[0]);
        const value = decodeURIComponent(parts[2]);
        if (isNonEmptyString(name) && isNonEmptyString(value)) {
            if (!Array.isArray(acc[name])) {
                acc[name] = [];
            }
            acc[name].push(value);
        }
        return acc;
    }, {});
}

export const encodeURIPath = (path) => {
    if (!path || path === '/') return path;
    return last(pathToObjects(path)).uri;
};

export const toBreadcrumbLink = ({ path }) => {
    const baseUrl = formatPath(getApplicationKey(window.location.pathname));
    if (!isNonEmptyString(path)) { return baseUrl; }
    return `${baseUrl}${encodeURIPath(path)}`;
};

// queryStrings is object of {somekey:[value, value1]}
export const joinQueryString = (queryStrings) => {
    const params = Object.entries(queryStrings).reduce((acc, [key, arr]) => {
        const eKey = encodeURIComponent(key);
        return [
            ...acc,
            ...arr.map(value => `${eKey}=${encodeURIComponent(value)}`),
        ];
    }, []).join('&');
    return `?${params}`;
};

function handleParts(acc, part) {
    if (['', '.'].includes(part)) {
        return acc;
    }
    if (part === '..') {
        acc.pop();
        return acc;
    }
    acc.push(part);
    return acc;
}

export function cleanPathname(p) {
    const path = isString(p) ? p : '';
    const result = path
        .split('/')
        .reduce(handleParts, [])
        .join('/');
    if (path.startsWith('./')) {
        return `./${result}`;
    }
    if (path.startsWith('/')) {
        return `/${result}`;
    }
    return result;
}

export const getFilenameFromUrl = (url) => {
    const pathname = (new URL(url))?.pathname;
    return pathname.slice(pathname.lastIndexOf('/') + 1).replaceAll(/[:+]/g, '');
};
