
export const EMPTYOBJECT = Object.freeze({});
export const getProcessObject = () => {
    try {
        return process;
    } catch (e) {
        return undefined;
    }
};

export const isNodeEnv = ({ getProcess = getProcessObject } = {}) => {
    const proc = getProcess();
    const obj = typeof proc !== 'undefined' ? proc : 0;
    return Object.prototype.toString.call(obj) === '[object process]';
};

// NOTE(dbildstr): these are set by webpack
const environment = Object.freeze({
    NODE_ENV: process.env.NODE_ENV,
    WEBUI_TRACING: process.env.WEBUI_TRACING,
});

export const getEnvironment = () => environment;

export const envHas = (key, value, { getEnv = getEnvironment } = {}) => {
    const env = getEnv();
    if (!(key in env)) {
        return false;
    }
    return env[key] === value;
};

export const isDevEnv = ({ hasEnv = envHas } = {}) =>
    hasEnv('NODE_ENV', 'development');
export const isProdEnv = ({ hasEnv = envHas } = {}) =>
    hasEnv('NODE_ENV', 'production');

export {
    environment as _environment,
};
