import { isProdEnv } from './platform';
import { isPromise } from './promise';
import { isNonEmptyString } from './string';

export const isFunction = func => typeof func === 'function';

export const defaultTo = (defaultValue, value) => (
    (value == null || Number.isNaN(value)) ? defaultValue : value
);
export const createGuarded = (fn, defaultValue) => v => ((v === undefined) ? defaultValue : fn(v));

export const identity = id => id;

export const compose = (...fns) => fns.reduce((f, g) => (...args) => {
    const resg = g(...args);
    if (isPromise(resg)) return resg.then(r => f(r));
    return f(resg);
});
export const pipe = (...fns) => compose.apply(compose, fns.reverse());

export const tap = func => (item) => {
    if (isFunction(func)) func(item);
    return item;
};

// eslint-disable-next-line no-console, no-unused-vars
export const createLogger = (log = console.log) => tap(i => log('TAP', i));

export const wrappedValue = (initialValue) => {
    let val = initialValue;
    return (newValue) => {
        if (newValue !== undefined) {
            val = newValue;
        }
        return val;
    };
};

export const invariant = (condition, message, isProductionMode = isProdEnv) => {
    if (condition) {
        return;
    }
    const prefix = 'Invariant failed';
    if (!isNonEmptyString(message) || isProductionMode()) {
        throw new Error(prefix);
    }
    throw new Error(`${prefix}: ${message}`);
};

export const warning = (condition, message, {
    isProductionMode = isProdEnv,
    logger = console,
} = {}) => {
    if (isProductionMode()) {
        return;
    }

    if (condition) {
        return;
    }

    const text = `Warning: ${message}`;
    if (typeof logger !== 'undefined') {
        logger.warn(text);
    }
};
