/* eslint-env worker */
/* eslint-disable no-restricted-globals */

import { isEmptyArray } from './array';
import { isFunction } from './functional';

export const workerMessage = {
    call: 'CALL',
    init: 'INIT',
    initAck: 'INIT_ACK',
};

export const callMessage = (method, params, port) => ({
    method,
    params,
    port,
    type: workerMessage.call,
});
export const initMessage = () => ({
    type: workerMessage.init,
});
export const initAckMessage = methods => ({
    methods,
    type: workerMessage.initAck,
});

export const wrapWorkerMethod = (worker, method) => (...params) => {
    const { port1, port2 } = new MessageChannel();
    let ok;
    let fail;
    port1.addEventListener('message', ({ data }) => {
        const { error, result } = data;
        if (error) {
            fail(error);
        } else {
            ok(result);
        }
    }, { once: true });
    port1.start();

    const promise = new Promise((resolve, reject) => {
        ok = resolve;
        fail = reject;
    });
    worker.postMessage(callMessage(method, params, port2), [port2]);
    return promise;
};

export function wrapWorkerMethods(worker, methods) {
    return methods.reduce((obj, method) => {
        // eslint-disable-next-line no-param-reassign
        obj[method] = wrapWorkerMethod(worker, method);
        return obj;
    }, {});
}

export function initWorker(workerPath, options) {
    const worker = new Worker(workerPath, options);
    worker.addEventListener('message', ({ data }) => {
        if (data.type === workerMessage.initAck) {
            worker.methods = wrapWorkerMethods(worker, data.methods);
        } else {
            throw new Error(`Unknown message "${data.type}"`);
        }
    });
    worker.postMessage(initMessage());
    return worker;
}

export function getMethods(obj) {
    if (!obj) { return []; }
    const propertyNames = obj.constructor !== Object
        ? Object.getOwnPropertyNames(Object.getPrototypeOf(obj))
        : Object.getOwnPropertyNames(obj);

    return propertyNames.reduce((acc, propertyName) => {
        if (typeof obj[propertyName] === 'function'
            && propertyName !== 'constructor'
        ) {
            acc.push(propertyName);
        }
        return acc;
    }, []);
}

export async function callMethod(exposedObject, { method, params, port }) {
    try {
        const workerMethod = exposedObject[method];
        if (!isFunction(workerMethod)) {
            throw new TypeError(`Worker method "${method}" is not a function`);
        }
        const result = await workerMethod.apply(exposedObject, params);
        port.postMessage({ result });
    } catch (error) {
        port.postMessage({ error });
    }
    port.close();
}

export function expose(exposedObject) {
    const methods = getMethods(exposedObject);
    if (isEmptyArray(methods)) {
        throw Error('Parameter is not an object or the object has no functions to expose');
    }
    self.addEventListener('message', ({ data }) => {
        switch (data.type) {
        case workerMessage.init:
            return self.postMessage(initAckMessage(methods));
        case workerMessage.call:
            return callMethod(exposedObject, data);
        default:
            throw new Error(`Unknown message "${data.type}"`);
        }
    });
}
