// NOTE(dbildstr): single quote should be quoted, due to it being treated
// as the beginning of a quoted string in jsonrpc calls otherwise
const quoteChars = [
    '"',
    ' ',
    '}',
    '\'',
    '\\',
    '\t',
    '[',
];

function quoteKey(key, forceQuotes = false) {
    if (Number.isFinite(key)) {
        return key;
    }
    let needQuote = forceQuotes;
    const escapeChars = [
        '\\',
        '"',
    ];
    const str = key.split('').reduce((buf, char) => {
        if (quoteChars.includes(char)) {
            needQuote = true;
        }
        if (escapeChars.includes(char)) {
            buf.push('\\');
        }
        buf.push(char);
        return buf;
    }, []).join('');
    if (needQuote || str === '') {
        return `"${str}"`;
    }
    return str;
}

module.exports = {
    quoteKey,
};
