/**
 * @typedef WrappedRequest
 * @prop {boolean} aborted
 * @prop {AbortSignal} signal
 * @prop {AbortSignal=} requestSignal
 * @prop {AbortController} ctrl
 * @prop {function} abort
 * @prop {function} clear
 */

/**
 * @typedef RequestOptions
 * @prop {AbortSignal=} signal
 */

/**
 * @callback FetchRequest
 * @param {string} url
 * @param {RequestOptions} options
 */

let requestsBlocked = false;

// list of pending fetch requests
const requests = new Set();

// list of fetch requests that are queued up while we block new requests
const requestQueue = new Set();

/**
 * Queues a request, wrapped in an AbortController, that will be
 * aborted when are blocked before the request is complete,
 * for example during pagehide events.
 *
 * @param {WrappedRequest} request
 */
export const addRequest = (request) => {
    requests.add(request);
};

/**
 * Queues a request, wrapped in an AbortController, that will be
 * aborted when requests are blocked before the request is complete,
 * for example during pagehide events.
 *
 * @param {WrappedRequest} request
 */
export const removeRequest = (request) => {
    requests.delete(request);
};
export const getRequests = () => Array.from(requests);
export const clearRequests = () => requests.clear();

/**
 * Aborts initiated fetch requests.
 */
export const abortRequests = () => {
    const reqs = Array.from(requests);
    requests.clear();
    for (const req of reqs) {
        try {
            req.abort();
        } catch (error) {
            // Ignore DOMExceptions and AbortErrors, otherwise rethrow it.
            if (!(error instanceof window.DOMException)
                || error.name !== 'AbortError'
            ) {
                throw error;
            }
        }
    }
};

/**
 * Returns if fetch requests are currently blocked or not
 */
export const requestsAreBlocked = () => requestsBlocked === true;

export const resetRequestState = () => { requestsBlocked = false; };

export function allowRequests() {
    requestsBlocked = false;

    const reqs = Array.from(requestQueue);
    requestQueue.clear();

    for (const req of reqs) {
        req();
    }
}

/**
 * Block requsts and optionally abort pending requests.
 *
 * @param {boolean} abort
 */
export function blockRequests(abort = false) {
    requestsBlocked = true;
    if (abort) {
        abortRequests();
    }
}

/**
 * Queues a callback that is called when requests are no longed blocked
 * @param {function} callback - function to call
 */
export function addPendingRequest(callback) {
    requestQueue.add(callback);
}

/**
 * Returns a copy of the pending request queu
 * @returns {function[]} - array of pending request functions
 */
export const getPendingRequests = () => Array.from(requestQueue);

/**
 * Clears the current queue of pending requests
 */
export const clearPendingRequests = () => requestQueue.clear();

/**
 *
 * @param {FetchRequest} fetcher
 * @param {string} url
 * @param {RequestOptions} options
 */
function requeueRequest(fetcher, url, options) {
    return new Promise((res) => { addPendingRequest(res); })
        .then(() => fetcher(url, options));
}

/**
 * Creates an AbortController and AbortSignal that can be used by fetch
 * requests. If an AbortSignal is passed in, the WrappedRequest will
 * also abort any pending requests where it is used.
 *
 * @param {AbortSignal=} requestSignal (optional) signal from fetchRequest call which will
 *                        be wrapped by the WrappedRequest
 * @returns {WrappedRequest}
 */
export const wrapRequest = (requestSignal) => {
    const ctrl = new AbortController();
    const signal = ctrl.signal;

    function onAbort() {
        if (!requestsBlocked) {
            ctrl.abort();
        }
    }

    const wrappedRequest = {
        aborted: false,
        ctrl,
        signal,
        requestSignal,
    };

    function clear() {
        removeRequest(wrappedRequest);
        if (requestSignal && requestSignal.removeEventListener) {
            requestSignal.removeEventListener('abort', onAbort);
        }
    }

    function abort() {
        clear();
        try {
            wrappedRequest.aborted = true;
            ctrl.abort();
        } catch {
            // catch block empty on purpose
        }
    }

    if (requestSignal && requestSignal.addEventListener) {
        if (requestSignal.aborted) {
            ctrl.abort();
        } else {
            requestSignal.addEventListener('abort', onAbort);
        }
    }
    wrappedRequest.abort = abort;
    wrappedRequest.clear = clear;
    addRequest(wrappedRequest);
    return wrappedRequest;
};

const baseFetchFunc = (url, options) => {
    const func = (
        (typeof window !== 'undefined' ? window.fetch : undefined)
        || (typeof fetch !== 'undefined' ? fetch : undefined)
    );
    return func(url, options);
};

const EMPTY_OBJECT = Object.freeze({});

/**
 * Wraps the default fetch function to abort pending requests on navigation
 * and requeue blocked/aborted requests.
 *
 * @param {string} url
 * @param {RequestOptions} options
 *
 * @returns {Promise<Response>}
 */
export function fetchRequest(url, options) {
    const { signal } = options || EMPTY_OBJECT;
    if (requestsAreBlocked()) {
        return requeueRequest(fetchRequest, url, options);
    }
    const abortableRequest = wrapRequest(signal);
    return baseFetchFunc(
        url,
        {
            ...options,
            signal: abortableRequest.signal,
        },
    ).then(
        (res) => {
            abortableRequest.clear();
            return res;
        },
        (error) => {
            if (requestsAreBlocked() && abortableRequest.aborted) {
                // NOTE: retry the request when we stop blocking fetch requests
                return requeueRequest(fetchRequest, url, options);
            }
            abortableRequest.clear();
            throw error;
        },
    );
}
